*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  *::after{
    box-sizing: border-box;
  }
  *::before{
    box-sizing: border-box;
  }
  ul,ol,dl{
    list-style:none;
  }
  a{
    color: inherit;
    text-decoration: none;
  }
  body{
    background: white;
  }